import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import Stars from "../../Stars";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import FilterOptionWithTotal from "../FilterOptionWithTotal";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type FilterOptionWithTotalStoryProps = {
    mode: string,
    brand: BrandType,
    open: boolean,
    label: string
}

const FilterOptionWithTotalStory = ({
    mode, brand, open, label
}: FilterOptionWithTotalStoryProps) => {
    const classes = useStyles(themes[brand]);
    const items = [
        { value: 1, checked: false, label: <Stars stars={1} /> },
        { value: "teste", checked: false, label: "text", total: 10 },   
    ];

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="FilterOptionWithTotal">
                <Typography variant="h3">FilterOptionWithTotal</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <FilterOptionWithTotal 
                        isMobile={false}
                        noModal={false}
                        sufix="Estrela"
                        open={open}
                        onChange={undefined}
                        items={items}
                        onApply={undefined}
                        onReset={undefined}
                        label={label}
                        filter={undefined} 
                        height={undefined} 
                        onClose={undefined} 
                        onActive={undefined}                    
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        label: "Label Text",
        open: false
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        open: { type: "boolean" },
        label: { type: "string" }
    }
}

storiesOf("Components/Filtros/FilterOptionWithTotal", module)
    .add("Default", (args) => <FilterOptionWithTotalStory {...args}/>, argsControls);
